export default {
	data() {
		return {
			payment: [],
			paymentId: '01'

		};
	},
	onLoad: function(e) {
		uni.showLoading({
			title: "加载中...."
		});
		uni.request({
			url: 'http://39.105.50.203/search/paymentMethod.json',
			method: 'GET',
			data: {},
			success: res => {
				console.log(res)
				this.payment = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		paymentSelect: function(e) {
			this.paymentId = e;
		},
		submitOrder:function(e){
			uni.showLoading({
			title: "提交中...."
		});
		}
	},
	onShow: function() {

	}
}
